import '@/static/css/index.css';
import app from '@/common/app.js';
import util from '@/common/util.js';
export default {

    data() {
        return {
            imagePath: app.imagePath,
            truePhoto: '',
            is_takePhoto: false,
            animateTop: '',
            an: 0,
            is_auth: false
        }
    },
    onShow() {},
    onLoad() {
        this.animate()
    },
    methods: {
        animate() {
            if (this.an <= 430) {
                this.an = this.an + 1;
                this.animateTop = this.an + 'rpx';

                setTimeout(() => {
                    this.animate();
                }, 10);
            } else {
                this.an = 0;
                this.animateTop = this.an + 'rpx';
                setTimeout(() => {
                    this.animate();
                }, 10);
            }
        },
        sendTakePhoto(){
            this.is_takePhoto = false;
            this.is_auth = false;
        },
        takePhoto() {
            // //util.getStatPoint('click', 'EVENT_DESCRIBE_5');
            this.is_takePhoto = false;
            this.is_auth = false;
            const ctx = uni.createCameraContext();
            ctx.takePhoto({
                quality: 'high',
                success: (res) => {
                    const tempFilePaths = res.tempImagePath;
                    console.log(res.tempImagePath);
                    uni.uploadFile({
                        url: app.serverPath + '/file/upload', //仅为示例，非真实的接口地址
                        filePath: tempFilePaths,
                        name: 'file',
                        header: {
                            'content-type': 'multipart/form-data',
                        },
                        success: (suc) => {
                            var result = JSON.parse(suc.data);
                            console.log(result.code == app.RESPONSE_STATUS.REQUEST_SUCCESS);
                            if (result.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                                this.truePhoto = result.data;
                                this.is_takePhoto = true;
                            } else {
                                uni.showToast({
                                    icon: 'none',
                                    title: result.msg
                                });
                            }
                        },
                        fail: (error) => {
                            console.log(error);
                        }
                    });
                }
            });
        },

        uploadtruePhoto() {
            uni.chooseImage({
                count: 1,
                sizeType: ['original', 'compressed'], //是原图还是压缩图，默认二者都有
                sourceType: ['camera '],
                success: (res) => {
                    const tempFilePaths = res.tempFilePaths;
                    uni.uploadFile({
                        url: app.serverPath + '/file/upload', //仅为示例，非真实的接口地址
                        filePath: tempFilePaths[0],
                        name: 'file',
                        header: {
                            'content-type': 'multipart/form-data',
                        },
                        success: (suc) => {
                            var result = JSON.parse(suc.data);
                            console.log(result.code == app.RESPONSE_STATUS.REQUEST_SUCCESS);
                            if (result.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                                this.truePhoto = result.data;
                                this.is_takePhoto = true;
                            } else {
                                uni.showToast({
                                    icon: 'none',
                                    title: result.msg
                                });
                            }
                        },
                        fail: (error) => {
                            console.log(error);
                        }
                    });
                }
            });
        },
        gotoReg() {
            //util.getStatPoint('click', 'EVENT_DESCRIBE_6');
            uni.navigateTo({
                url: "./regist?"
            })
        },
        startAuthen() {
            this.is_auth = true;
            uni.showLoading({
                title: '识别中',
                mask: true
            });
            console.log("==================开始搜索=========================");
            uni.request({
                url: app.serverPath + '/app/findSmByFace',
                method: 'POST',
                header: {
                    'content-type': 'application/x-www-form-urlencoded'
                },
                data: {
                    fileId: this.truePhoto,
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        //判断人脸库是否存在
                        var userId = res.data.data;
                        uni.navigateTo({
                            url: "./regist?userId=" + userId+ "&truePhoto="+this.truePhoto
                        })
                    } else {
                        uni.navigateTo({
                            url: "./regist?truePhoto="+this.truePhoto
                        })
                    }
                    uni.hideLoading();
                    console.log(res);
                },
                fail: (res) => {
                    uni.showToast({
                        icon: 'none',
                        title: '请求错误'
                    });
                    uni.hideLoading();
                }
            })
        }
    }
}
